.wrapper {
    margin: 100px 0 150px;
  }
.header {
    padding: 25px 0;
    text-align: center;
    background: #222;
  }

.header img {
width: 100px;
height: 13px;
}
.tesla-battery {
    width: 1050px;
    margin: 0 auto;
  }

.tesla-battery h1 {
font-family: 'RobotoNormal';
    font-weight: 100;
    font-size: 38px;
    text-align: center;
    letter-spacing: 3px;
}

.tesla-climate-container {
float: left;
width: 420px;
padding: 0 40px;
margin: 0 40px 0 0;
border-left: 1px solid #ccc;
border-right: 1px solid #ccc;
}
.tesla-controls {
display: block;
width: 100%;
}
.tesla-battery__notice {
    margin: 20px 0;
    font-size: 15px;
    color: #666;
    line-height: 20px;
}
.tesla-car {
    width: 100%;
    min-height: 350px;
    background: #fff url(/TeslaRC/static/media/tesla.ff16203f.jpg) no-repeat top center;
    background-size: contain; }
  
  .tesla-wheels {
    height: 247px;
    width: 555px;
    position: relative;
    margin: 0 auto; }
  
  .tesla-wheel {
    height: 80px;
    width: 80px;
    bottom: 0;
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover; }
    .tesla-wheel--front {
      left: 53px; }
    .tesla-wheel--rear {
      right: 72px; }
    .tesla-wheel--19 {
      background-image: url(/TeslaRC/static/media/wheel-19.3b975a00.png);
      -webkit-animation: infinite-spinning 250ms steps(6) infinite;
      animation: infinite-spinning 250ms steps(6) infinite; }
    .tesla-wheel--21 {
      background-image: url(/TeslaRC/static/media/wheel-21.e787dc92.png);
      -webkit-animation: infinite-spinning 480ms steps(12) infinite;
      animation: infinite-spinning 480ms steps(12) infinite; }

  @keyframes infinite-spinning {
    from {
      transform: rotate(0deg); }
    to {
      transform: rotate(360deg); } }
  @-webkit-keyframes infinite-spinning {
    from {
      transform: rotate(0deg); }
    to {
      transform: rotate(360deg); } }
.tesla-stats {
    margin: -70px 0 30px; }
    .tesla-stats ul {
      text-align: center; }
      .tesla-stats ul li {
        display: inline-block;
        width: 130px;
        position: relative; }
        .tesla-stats ul li p {
          font-size: 40px;
          font-weight: normal;
          font-family: 'RobotoNormal';
          display: block;
          padding: 0 18px 0 0;
          position: relative;
          color: #008dff;
          text-align: right; }
          .tesla-stats ul li p:after {
            font-size: 14px;
            font-weight: normal;
            font-family: 'RobotoNormal';
            content: 'MI';
            position: absolute;
            top: 8px;
            right: 0; }
    .tesla-stats-icon {
      height: 20px;
      background-size: auto 13px;
      background-position: top right;
      background-repeat: no-repeat; }
      .tesla-stats-icon--60 {
        background-image: url(/TeslaRC/static/media/60.7ee66eea.svg); }
      .tesla-stats-icon--60d {
        background-image: url(/TeslaRC/static/media/60d.246b5ba5.svg); }
      .tesla-stats-icon--75 {
        background-image: url(/TeslaRC/static/media/75.37862a62.svg); }
      .tesla-stats-icon--75d {
        background-image: url(/TeslaRC/static/media/75d.9c937cb9.svg); }
      .tesla-stats-icon--90d {
        background-image: url(/TeslaRC/static/media/90d.28a215f0.svg); }
      .tesla-stats-icon--p100d {
        background-image: url(/TeslaRC/static/media/p100d.62ed87b4.svg); }
  
.tesla-counter {
    float: left;
    width: 230px; }
    .tesla-counter__title {
      letter-spacing: 2px;
      font-size: 16px; }
    .tesla-counter__container {
      margin: 10px 0 0;
      padding-right: 40px; }
      .tesla-counter__container input[type=number] {
        border: 0;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px; }
    .tesla-counter__number {
      font-family: 'RobotoNormal';
      font-size: 25px;
      line-height: 25px;
      font-weight: 400;
      position: relative; }
      .tesla-counter__number span {
        position: absolute;
        top: 0;
        left: 35px;
        font-size: 15px;
        text-transform: uppercase; }
    .tesla-counter__item {
      position: relative;
      width: 100%;
      height: 65px;
      border: 1px solid #ccc;
      display: inline-block;
      padding: 18px 0 0 30px;
      margin: 0 8px 0 0;
      background-color: #f7f7f7;
      background-position: 24.21053% 9px;
      background-repeat: no-repeat;
      background-size: 44px; }
      .tesla-counter__item:focus {
        background-color: #f2f2f2;
        outline: none; }
    .tesla-counter__controls {
      position: absolute;
      right: 10px;
      top: 7px; }
      .tesla-counter__controls button {
        outline: 0;
        width: 30px;
        color: #008dff;
        cursor: pointer;
        display: block;
        padding: 11px 0;
        vertical-align: middle;
        border: 0;
        background-size: 60%;
        background-position: center;
        background-repeat: no-repeat;
        background-color: transparent; }
        .tesla-counter__controls button[disabled] {
          opacity: 0.4;
          cursor: not-allowed; }
        .tesla-counter__controls button:first-child {
          border-bottom: 1px solid #fff;
          background-image: url(/TeslaRC/static/media/up.b3fc73bf.svg); }
        .tesla-counter__controls button:last-child {
          border-top: 1px solid #ccc;
          background-image: url(/TeslaRC/static/media/down.a56566ba.svg); }
  
.tesla-climate {
    float: left; }
    .tesla-climate__item {
      cursor: pointer;
      display: block;
      width: 100px;
      height: 100px;
      border: 6px solid #f7f7f7;
      border-radius: 50%;
      box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
      color: #666;
      background: #fff; }
      .tesla-climate__item--active {
        color: #fff;
        background: #33a0ff;
        background: linear-gradient(to bottom, #33a0ff 0%, #388bff 100%); }
        .tesla-climate__item--active.tesla-heat {
          background: #d64800;
          background: linear-gradient(to bottom, #d64800 0%, #d20200 100%); }
    .tesla-climate__icon {
      display: block;
      width: 22px;
      height: 22px;
      margin: 8px auto 0;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url(/TeslaRC/static/media/ac-off.c418a045.svg); }
      .tesla-heat .tesla-climate__icon {
        background-image: url(/TeslaRC/static/media/heat-off.987170ea.svg); }
      .tesla-climate__item--active .tesla-climate__icon {
        background-image: url(/TeslaRC/static/media/ac-on.4dcccadf.svg); }
      .tesla-climate__item--active.tesla-heat .tesla-climate__icon {
        background-image: url(/TeslaRC/static/media/heat-on.2c61b1a6.svg); }
    .tesla-climate p {
      margin: 14px 0 0;
      text-align: center;
      font-size: 10px;
      text-transform: uppercase; }
    .tesla-climate input[type=checkbox] {
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px; }
.tesla-wheels__component {
    float: left;
    width: 355px;
  }
  .tesla-wheels__title {
    letter-spacing: 2px;
    font-size: 16px;
  }
  .tesla-wheels__container {
    margin: 10px 0 0;
  }
  .tesla-wheels__item {
    cursor: pointer;
    width: 47%;
    height: 65px;
    border: 1px solid #ccc;
    display: inline-block;
    padding: 20px 0 0 90px;
    margin: 0 8px 0 0;
    background-color: #f7f7f7;
    background-position: 24.21053% 9px;
    background-repeat: no-repeat;
    background-size: 44px;
  }
  .tesla-wheels__item--19 {
    background-image: url(/TeslaRC/static/media/19.c8ef3398.svg);
  }
  .tesla-wheels__item--21 {
    background-image: url(/TeslaRC/static/media/21.a52693ae.svg);
  }
  .tesla-wheels__item--focused {
    background-color: #f2f2f2;
  }
  .tesla-wheels__item--active {
    border-color: #39f;
    box-shadow: inset 0px 0px 0px 1px #39f;
  }
  .tesla-wheels__item p {
    font-family: 'RobotoNormal';
    font-size: 16px;
    font-weight: 400;
    color: #333;
  }
  .tesla-wheels__item input[type=radio] {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
@font-face {
    font-family: 'RobotoNormal';
    src: url(/TeslaRC/static/media/Roboto-Regular-webfont.30799efa.eot);
    src: url(/TeslaRC/static/media/Roboto-Regular-webfont.30799efa.eot?#iefix) format('embedded-opentype'),
         url(/TeslaRC/static/media/Roboto-Regular-webfont.3e5675c8.woff) format('woff'),
         url(/TeslaRC/static/media/Roboto-Regular-webfont.7f1320f7.ttf) format('truetype'),
         url(/TeslaRC/static/media/Roboto-Regular-webfont.da61d7ef.svg#RobotoRegular) format('svg');
    font-weight: normal;
    font-style: normal;
  }

  *, *:before, *:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font: 300 14px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
  }

  .cf:before,
  .cf:after {
      content: '';
      display: table;
  }
  .cf:after {
      clear: both;
  }
  .cf {
    *zoom: 1;
  }
